'use strict';
import {JsonPortMarshaler} from "./marshaler";
import {IHostProvider} from "../interfaces/iHostProvider";
import {Promise} from "q";

export class HostMarshaler extends JsonPortMarshaler {
    constructor(public hostProvider: IHostProvider) {
        super();
    }

    public showDocument(path: string, line: number, column: number): Promise<void> {
        return this.hostProvider.showDocument(path, line, column);
    }

    public getDocumentLocation(path: string): Promise<string> {
        return this.hostProvider.getDocumentLocation(path);
    }
}